/* Wufoo Form Styles - Scoped to .wufoo-form-container to avoid affecting other pages */

.wufoo-form-container {
  font-family: "Lucida Grande", "Lucida Sans Unicode", Tahoma, sans-serif;
  letter-spacing: 0.01em;
}

.wufoo-form-container .wufoo {
  margin: 0;
  padding: 0;
  position: relative;
}

.wufoo-form-container form ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.wufoo-form-container form li {
  margin: 0;
  padding: 6px 1% 9px 1%;
  clear: both;
  background-color: transparent;
  position: relative;
  transition: background-color 350ms ease-out;
}

.wufoo-form-container form li:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.wufoo-form-container label.desc {
  font-size: 95%;
  font-weight: bold;
  color: #222;
  line-height: 150%;
  margin: 0;
  padding: 0 0 3px 0;
  border: none;
  display: block;
  white-space: normal;
  width: 100%;
}

.wufoo-form-container span.req {
  display: inline;
  float: none;
  color: red !important;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.wufoo-form-container input.text,
.wufoo-form-container textarea.textarea,
.wufoo-form-container input.file {
  font-family: "Lucida Grande", Tahoma, Arial, sans-serif;
  font-size: 100%;
  color: #333;
  margin: 0;
  padding: 2px 0;
  border-top: 1px solid #7c7c7c;
  border-left: 1px solid #c3c3c3;
  border-right: 1px solid #c3c3c3;
  border-bottom: 1px solid #ddd;
  background: #fff url(/images/fieldbg.gif) repeat-x top;
  width: 100%;
  box-sizing: border-box;
}

.wufoo-form-container textarea.textarea {
  width: 100%;
  min-width: 100%;
  max-width: 100%;
  height: 10em;
  resize: vertical;
}

.wufoo-form-container input.medium,
.wufoo-form-container textarea.medium {
  width: 100%;
}

.wufoo-form-container input.file {
  padding: 4px;
}

.wufoo-form-container .file-info {
  display: block;
  margin-top: 5px;
  color: #666;
  font-size: 85%;
}

.wufoo-form-container form li.focused {
  background-color: #fff7c0 !important;
}

.wufoo-form-container form li.buttons {
  width: auto !important;
  position: relative;
  clear: both;
  padding: 10px 1% 10px 1%;
}

.wufoo-form-container input.btTxt {
  padding: 0 7px;
  width: auto;
  overflow: visible;
  font-size: 100%;
  margin-right: 5px;
  background: #4a90e2;
  color: white;
  border: none;
  padding: 8px 20px;
  cursor: pointer;
  border-radius: 3px;
  font-weight: normal;
}

.wufoo-form-container input.btTxt:hover {
  background: #357abd;
}

.wufoo-form-container input.btTxt:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.wufoo-form-container input.btTxt.submit {
  background: #4a90e2;
  color: white;
}

.wufoo-form-container input.btTxt.submit:hover {
  background: #357abd;
}

